@media screen and(max-width:750px) {
  //header
  .section-title {
    font-size: 3.5rem;
  }
  .main-header {
    top: 50%;
    left: 90%;
    flex-direction: column;
    &__ul {
      width: 50px;
      height: 50px;
      font-size: 2rem;
    }
    .left-ul,
    .right-ul,
    .center-ul {
      &:hover,
      &:focus {
        box-shadow: none;
      }
    }
  }

  //hero
  .main-hero {
    flex-direction: column-reverse;
    background-size: cover;
    background: url(https://i.pinimg.com/originals/2b/17/90/2b1790a454afdb18da1ac7c459330b05.gif);
    background-repeat: no-repeat;
    &__text {
      h3 {
        font-size: 3.5rem;
      }
      h2 {
        font-size: 5rem;
      }
      &__subtitle {
        font-size: 5rem;
        font-family: $sec-ff;
      }
      &__maintitle {
        font-size: 12.5rem;
        font-family: $primary-ff;
      }
    }
    .main-hero__img {
      &__esllipse {
        width: 250px;
        height: 384px;
      }
    }
  }
  //project
  .main-project {
    height: 190vh;

    &__card {
      margin: 0;
      flex-direction: column;
      &__items {
        margin: 0.8em 0;
      }
    }
  }
  //skill

  .main-skill {
    width: 100%;
    display: grid;
    align-items: center;
    justify-content: center;
    .main-section__skill {
      &__list {
        margin: 0.1em 0;
        flex-direction: column;

        &__items {
          align-items: center;

          width: 300px;
          height: 110px;
          margin: 0.5em 0;
          &__circle {
            width: 90px;
            height: 90px;
          }
        }
      }
    }
  }

  //about
  .main-about {
    &__info {
      padding: 0 0.2em;
      font-size: 1.6rem;
    }
  }
  .main-contact {
height: 130vh;
    overflow: hidden;
    &__main {
      flex-direction: column;
      margin-top: 10vh;
      &__img {
        width: 100%;
        .design {
          width: 100px;
          height: 100px;
          background-color: rgba(255, 255, 255, 0.354);
        }
        .design-1 {
          top: -50px;
          left: -50px;
        }
        .design-2 {
          bottom: -50px;
          left: -50px;
        }
        .design-3 {
          display: block;
          top: -50px;
          right: -50px;
        }
        .design-4 {
          display: block;
          bottom: -50px;
          right: -50px;
        }
        h3 {
          font-size: 3.5rem;
        }
        a {
          font-size: 1.5rem;
        }
        &_items {
          &__flex {
            font-size: 1.8rem;
          }
        }
      }
    }
  }
  .form {
    width: 100vw;
    display: grid;
    place-content: center;
    .label-list {
      input {
        font-size: 0.9rem;
        width: 350px;
      }
      textarea {
        font-size: 0.9rem;

        max-width: 350px;
        width: 350px;
        min-width: 350px;
      }
    }
  }
}
